.list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
}
.item {
  width: calc((100% - 80px) / 5);
}
.image {
  border-radius: 14px;
}
.link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  width: 100%;
  text-decoration: none;
  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.link:hover,
.link:focus {
  scale: 1.1;
}
.title {
  font-size: 18px;
  font-family: 'Oxygen', sans-serif;
  color: #81a5ff;
  text-decoration: none;
  width: 220px;
  text-align: center;
}
